export class servicePokemom {
    secionarPokemons() {
        const url = `https://pokeapi.co/api/v2/pokemon/`;
        return fetch(url)
            .then((res) => this.processarResposta(res))
            .then((obj) => this.mapearListaPokemons(obj.results));
    }
    mapearListaPokemons(objetos) {
        const pokemons = objetos.map(obj => this.selecionarPokemomPorNome(obj.name));
        return Promise.all(pokemons);
    }
    processarResposta(resposta) {
        if (resposta.ok)
            return resposta.json();
        throw new Error('Pokémon não encontrado!');
    }
    selecionarPokemomPorNome(nome) {
        const url = `https://pokeapi.co/api/v2/pokemon/${nome}`;
        return fetch(url)
            .then(res => res.json())
            .then((obj) => this.mapearPokemom(obj));
    }
    mapearPokemom(obj) {
        return {
            id: obj.id,
            nome: obj.name,
            sprite: obj.sprites.front_default
        };
    }
}
